import React from 'react'
import Box from '@material-ui/core/Box'
import Container from '@material-ui/core/Container'
import Chooser from "../../components/Chooser";
import FilterListIcon from "@material-ui/icons/FilterList";
import Filter from "../../components/Filter";
import {SimpleAccordion} from "./SimpleAccordion";
import zemlyaIf from '../../img/images/zemlya_if.png';
import agenstvo from '../../img/images/agenstvo.png'
import CallbackFormPopUp from "../../components/CallbackFormPopUp";
import Dialog from "@material-ui/core/Dialog";
import Slide from "@material-ui/core/Slide";


const Transition = React.forwardRef(function Transition(props, ref) {
    return <Slide direction="up" ref={ref} {...props} />;
});


class ServicesPage extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
        showFilter: false,
        showDialog: false
    }
  }

  componentDidMount() {
    window.scrollTo(0, 0)
  }

  componentWillUnmount() {
  }

  setShowDialog = () => {
      this.setState({showDialog: true})
  }
  setCloseDialog = () => {
      this.setState({showDialog: false})
  }

  setShowFilter = () => {
        this.setState({ showFilter: true })
  }

  setHideFilter = () => {
        this.setState({ showFilter: false })
  }
  render() {
    return (
      <React.Fragment>
          <Dialog
              open={this.state.showDialog}
              fullWidth={true}
              maxWidth='sm'
              TransitionComponent={Transition}
              onClose={() => {
                  this.setCloseDialog()
              }}
          >
              <CallbackFormPopUp />
          </Dialog>
      <Container maxWidth="lg">
          {this.state.showFilter === true ? <Filter hidden={this.setHideFilter} expanded={true} /> : ''}
        <Box my={2}>
            <h2 style={{marginBottom: '22px', marginTop: '40px'}}> - <span style={{color: '#01eb88'}}> </span></h2>
            <h4 style={{marginBottom: '5px'}}>    !</h4>
            <h4 style={{marginBottom: '40px'}}>  <span style={{color: '#01eb88'}}>        :</span></h4>
        </Box>

          <div style={{display:'flex', justifyContent:'space-between',alignItems:'center'}}>
              <div style={{marginRight: '40px'}}>
                  <img src={zemlyaIf} alt=""/>
              </div>
              <Box>
                  <h3 style={{
                      textAlign:'left',
                      color: '#3C4B5A',
                      fontFamily: 'Roboto, sans-serif',
                      fontWeight: '900',
                      marginBottom: '40px'
                  }}>    , <span style={{color: '#01eb88'}}>:</span></h3>

                  <SimpleAccordion/>
              </Box>
          </div>

        <div style={{display:'flex', justifyContent:'space-between', alignItems:'center'}}>
            <Box my={2}>
                <h3 style={{
                    textAlign:'left',
                    color: '#3C4B5A',
                    fontFamily: 'Roboto, sans-serif',
                    fontWeight: '900',
                    marginBottom: '40px'
                }}>  <span style={{color: '#01eb88'}}>  </span><br/>  <span style={{color: '#01eb88'}}>  :</span></h3>

                <Box m={2}>
                    <h4 style={{color: 'black', fontWeight:'800'}}>   :</h4>
                    <h5>        .</h5>
                </Box>

                <Box m={2}>
                    <h4 style={{color: 'black'}}>  :</h4>
                    <h5>&mdash;   </h5>
                    <h5>&mdash;    </h5>
                    <h5>&mdash;      </h5>
                    <h5>&mdash;     </h5>
                    <h5>&mdash;   ,     </h5>
                    <h5>&mdash;     </h5>
                </Box>
            </Box>
            <div style={{marginLeft: '30px'}}>
                <img src={agenstvo} alt=""/>
            </div>
        </div>
            <div style={{display:'flex',justifyContent:'flex-end'}}>
                <button style={{width: '350px'}} className='main-button main-button-large'
                        onClick={()=>this.setShowDialog()}
                >
                     
                </button>
            </div>


          <Container maxWidth='lg' >
              <Chooser />
          </Container>
          <div className='filter-button' onClick={this.setShowFilter}>
              <h5 style={{position: 'absolute', top: '-30px'}}></h5>
              <FilterListIcon style={{width:'55px',height:'55px',color:'#FFF'}} />
          </div>
      </Container>
      </React.Fragment>
    )
  }
}

export default ServicesPage
